.navbar {
  background-color: transparent;
  transition: all 0.5s ease-in-out; }
  .navbar-overlay {
    position: absolute;
    top: -80px;
    right: 0px;
    left: 0px;
    width: 100%;
    z-index: 1;
    pointer-events: none;
    height: 290px;
    background-image: linear-gradient(-180deg, rgba(14, 14, 14, 0.9) 0%, rgba(14, 14, 14, 0.76) 32%, rgba(43, 43, 43, 0) 100%); }
  .navbar-logo {
    position: absolute;
    align-self: center;
    top: 0px;
    bottom: 0px;
    left: 0px;
    right: 0px;
    align-items: center;
    justify-content: center;
    display: flex; }
    .navbar-logo span {
      font-size: 20px;
      color: white;
      font-family: 'Copperplate-Bold';
      letter-spacing: 1px; }
  .navbar > .container {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between; }
  .navbar .navbar-collapse {
    height: auto !important; }
    @media (max-width: 991px) {
      .navbar .navbar-collapse {
        background: #121821;
        padding: 20px;
        padding-top: 83px;
        position: fixed;
        top: 0;
        right: 0;
        height: auto;
        width: 0%;
        z-index: 1;
        opacity: 0;
        transition: all 600ms cubic-bezier(0.62, 0.04, 0.3, 1.56);
        transition-delay: 100ms; }
        .navbar .navbar-collapse.show {
          width: 250px;
          opacity: 1; } }
  .navbar .navbar-nav {
    display: flex;
    flex-direction: column;
    justify-content: center; }
    @media (min-width: 992px) {
      .navbar .navbar-nav {
        flex-direction: row; } }
  .navbar .navbar-toggler {
    height: 16px;
    width: 23px;
    cursor: pointer;
    padding: 0;
    border: 0;
    background: transparent;
    outline: 0;
    box-shadow: none;
    z-index: 9;
    position: relative; }
    .navbar .navbar-toggler > span {
      display: block;
      position: absolute;
      height: 2px;
      width: 100%;
      background: #fff none repeat scroll 0% 0%;
      opacity: 1;
      top: 0;
      left: 0px;
      transition: all 600ms cubic-bezier(0.62, 0.04, 0.3, 1.56);
      transition-delay: 100ms;
      transform: rotate(0deg); }
      .navbar .navbar-toggler > span:nth-child(1) {
        margin-top: 0px; }
      .navbar .navbar-toggler > span:nth-child(2) {
        margin-top: 7px;
        margin-right: 0px; }
      .navbar .navbar-toggler > span:nth-child(3) {
        margin-top: 14px; }
    .navbar .navbar-toggler.collapsed span {
      background-color: #fff !important; }
      .navbar .navbar-toggler.collapsed span:nth-child(1) {
        transform: rotate(135deg);
        margin-top: 7px; }
      .navbar .navbar-toggler.collapsed span:nth-child(2) {
        opacity: 0;
        left: -69px; }
      .navbar .navbar-toggler.collapsed span:nth-child(3) {
        transform: rotate(-135deg);
        margin-top: 7px; }
    @media (min-width: 992px) {
      .navbar .navbar-toggler {
        display: none; } }
  .navbar .nav-link {
    font-size: 14px;
    font-size: 1rem;
    margin: 0;
    line-height: normal;
    color: #fff;
    position: relative;
    font-family: 'Gilroy-Light'; }
    .navbar .nav-link:after {
      content: '';
      position: absolute;
      width: 100%;
      height: 1px;
      bottom: -1px;
      left: 0;
      background-color: #3283e6;
      visibility: hidden;
      transform: scaleX(0);
      transition: all 0.3s ease-in-out 0s; }
    .navbar .nav-link:hover:after, .navbar .nav-link.active:after {
      visibility: visible;
      transform: scaleX(1); }
    @media (max-width: 991px) {
      .navbar .nav-link {
        padding: 15px 0;
        border-bottom: 1px solid #19212d;
        font-size: 18px;
        font-size: 1.28571rem;
        font-weight: 300; }
        .navbar .nav-link:hover, .navbar .nav-link.active {
          color: #fff; } }
    @media (min-width: 992px) {
      .navbar .nav-link {
        margin-right: 24px; }
        .navbar .nav-link:after {
          height: 3px;
          bottom: -3px; } }
  .navbar.sticky {
    top: 0;
    background-color: #fff;
    box-shadow: 0 2px 16px 0 rgba(0, 0, 0, 0.14);
    padding: 15px 0; }
    @media (max-width: 991px) {
      .navbar.sticky .navbar-collapse {
        padding-top: 63px; } }
    .navbar.sticky .navbar-toggler > span {
      background-color: #121821; }
  @media (min-width: 992px) {
    .navbar {
      flex-wrap: nowrap;
      padding: 25px 0; }
      .navbar.sticky {
        padding: 19px 0px 18px 0;
        top: 0; }
        .navbar.sticky .nav-link {
          color: #8f8f8f; }
          .navbar.sticky .nav-link:hover, .navbar.sticky .nav-link.active {
            color: #3283e6; } }

